@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&?family=IBM+Plex+Mono:wght@300;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;

  --primary: rgb(110, 6, 155);
  --primary-light: rgb(154, 54, 196);
  --secondary: rgb(9, 128, 179);
  --secondary-light: rgb(64, 173, 220);
  --grey-dark: hsla(33, 0%, 10%, 1);
  --grey-medium: rgb(96, 96, 96);
  --grey-light: hsla(34, 0%, 79%, 1);
}

body {
  font-family: 'Open Sans', sans-serif;
  line-height: 1.7;
  font-size: 1.6rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* 
Typography
*/

h2 {
  color: rgb(110, 6, 155);
  color: var(--primary);
}

/* 
Layout
*/
.container {
  max-width: 1000px;
  margin: 0 auto;
}

.my-masonry-grid { /* Not needed if autoprefixing */ /* Not needed if autoprefixing */
  display: flex;
  margin-left: -5px; /* gutter size offset */
  width: auto;
}
.my-masonry-grid_column {
  padding-left: 5px; /* gutter size */
  background-clip: padding-box;
}

/* Style your items */
.my-masonry-grid_column > div {
  /* change div to reference your elements you put in <Masonry> */
  background: grey;
  margin-bottom: 5px;
}

.header {
  text-align: center;
  padding: 2rem 0;
}
.header__title-container a {
  text-decoration: none;
}

.header__svg {
  max-width: 70rem;
  display: block;
  margin: 0 auto;
}

/* .header__title {
  font-size: 6rem;
  color: var(--primary);
  text-transform: uppercase;
  letter-spacing: 4px;
  line-height: 1.2;
} */

.header__subtitle {
  display: inline-block;
  color: var(--primary);
  font-style: italic;
  font-size: 2rem;
  border-bottom: 1.5px solid var(--primary);
  padding: 2rem;
}

.header__nav {
  padding: 1rem 0;
  display: flex;
  justify-content: center;
  font-size: 2.5rem;
}

.header__nav-link {
  font-weight: 700;
  color: var(--primary);
  margin: 0 1.5rem;
  text-decoration: none;
  text-transform: uppercase;
}

.header__nav-link--active {
  border-bottom: 2px solid var(--primary);
}

.filters {
  text-align: center;
  margin-bottom: 3rem;
}

.filters__years,
.filters__tags {
  margin-bottom: 1rem;
}

.filters__label {
  font-size: 1.6rem;
  display: inline-block;
  /* text-transform: uppercase; */
  text-decoration: none;
  cursor: pointer;
  color: black;
  border-radius: 5px;
  padding: 0 8px;
  margin: 2px;
  border: 1px solid transparent;
  transition: all 0.2s;
}

.filters__label:hover {
  border: 1px solid var(--primary);
}

.filters__label--active {
  color: white;
  background-color: var(--primary);
}

.filters__label--year {
  /* padding: 0.25rem 1rem; */
}

.filters__label--tag {
  /* padding: 0.25rem 1.4rem; */
  font-style: italic;
}

.filters__meta {
  margin-top: 2rem;
}

.filters__label--meta {
  border: 1px solid black;
  text-transform: uppercase;
  color: black;
  background-color: transparent;
  line-height: 1.7;
  transition: all 0.2s;
  padding: 0.25rem 2rem;
  margin: 0 0.75rem;
}

.filters__label--meta:hover {
  border: 1px solid transparent;
  color: white;
  background-color: var(--primary);
}

.about {
  max-width: 80rem;
  margin: 0 auto;
}

.about__title {
  margin-bottom: 1rem;
}

.about p {
  margin-bottom: 2rem;
}

.about ul {
  list-style: none;
  margin-bottom: 2rem;
}

.about__fav {
  display: flex;
  justify-content: left;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.about__fav-cover {
  display: inline-block;
  max-width: 5rem;
  margin-right: 1.5rem;
}

.about__fav-title {
  color: var(--primary);
  font-size: 2.4rem;
  font-weight: 600;
  line-height: 1;
}

.about__fav-authors {
}

.single__cover {
  float: right;
  max-height: 35rem;
  margin: 1rem 0 1rem 2rem;
  border-radius: 0.5rem;
}

.single__header {
  font-weight: bold;
  line-height: 1.4;
}

.single__titles {
  margin-bottom: 2rem;
}

.single__title {
  font-size: 4rem;
  color: black;
  /* margin: 5px 0px; */
}

.single__subtitle {
  font-size: 2rem;
  color: var(--grey-dark);
  font-weight: normal;
  font-style: italic;
  /* margin: 5px 0px; */
}

.single__creators {
  margin-bottom: 2rem;
  line-height: 1.5;
  /* padding-left: 1rem;
  border-left: 2px solid var(--primary); */
}

.single__authors {
  font-size: 2.3rem;
  color: var(--primary);
  /* margin: 5px 0px; */
}
.single__ill-and-trans {
  font-size: 1.8rem;
  color: black;
  font-weight: normal;
  /* margin: 5px 0px; */
}

.single__body {
  margin-bottom: 2rem;
  font-size: 1.7rem;
}

.single__footer {
  font-size: 1.6rem;
}

.single__star {
}

.single__star--lit {
  color: var(--primary);
}

.single__star--dim {
  color: var(--grey-light);
}

.single__tags-and-links {
  margin-top: 3rem;
}

.single__tag-list {
  margin: 1rem 0;
}

.single__label--tag {
  font-size: 1.4rem;
  font-weight: 600;
  color: var(--primary);
  border: 1px solid var(--primary);
  background-color: white;
  padding: 0.5rem 0.75rem;
  border-radius: 0.75rem;
  text-decoration: none;
  transition: all 0.3s;
}

.single__label--tag:hover {
  color: white;
  border: 1px solid transparent;
  background-color: var(--primary);
  border-radius: 0.75rem;
  text-decoration: none;
  transition: all 0.3s;
}

.single__label--active {
  color: white;
  border: 1px solid transparent;
  background-color: var(--primary);
}

.single__label--active:hover {
  color: var(--primary);
  border: 1px solid var(--primary);
  background-color: white;
}

.single__external-tag,
.single__external-tag:visited {
  font-size: 1.4rem;
  font-weight: 600;
  color: white;
  padding: 0.5rem 0.75rem;
  background-color: var(--secondary-light);
  border-radius: 0.75rem;
  text-decoration: none;
  transition: all 0.3s;
}

.single__label--tag:not(:last-child),
.single__external-tag:not(:last-child) {
  margin-right: 0.5rem;
}

.single__bottom-nav-btn,
.single__bottom-nav-btn:visited {
  font-size: 1.6rem;
  font-weight: 600;
  color: var(--primary);
  padding: 0.5rem 1.25rem;
  border: 1px solid var(--primary-light);
  border-radius: 0.75rem;
  text-decoration: none;
  transition: all 0.3s;
  margin-top: 3rem;
}

.single__bottom-nav-btn:hover,
.single__bottom-nav-btn:visited:hover {
  background-color: var(--primary-light);
  color: white;
}

.single__bottom-nav-btn--previous {
  float: left;
}
.single__bottom-nav-btn--next {
  float: right;
}

.single__bottom-nav-btn--disabled,
.single__bottom-nav-btn--disabled:visited {
  color: var(--grey-light);
  border-color: var(--grey-light);
}

.single__bottom-nav-btn--disabled:hover,
.single__bottom-nav-btn--disabled:visited:hover {
  color: var(--grey-light);
  border-color: var(--grey-light);
  background-color: white;
  cursor: not-allowed;
}

.covers__cover {
  width: 100%;
  max-width: 275px;
  height: auto;
  margin: 0px auto;
}

.list__container {
  margin: 0 auto;
  max-width: 90rem;
}

.list__book {
  display: flex;
  margin-bottom: 2rem;
  width: 45%;
}

.list__book--left {
  float: left;
  clear: left;
}

.list__book--right {
  float: right;
}

.list__cover {
  max-width: 10rem;
  margin-right: 2rem;
}

.list__details {
  display: block;
}

.list__title,
.list__title:visited {
  text-decoration: none;
  color: var(--primary);
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 1;
}

.list__authors {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 0.8rem;
}

.list__stars {
}

